export default {
  name: 'yqhTab',
  data () {
    return {
      isClick:'0'
    }
  },
  props:{
    hasTabLine: {
      type: Boolean,
      default: false
    },
    tabList:{
      type: Array,
      default: ()=>{
        return [
          {name: '全部',value:'0'},
          {name: '选项1',value:'1'}
          ]
      }
    }
  },
  methods: {
    getTabIndex (item) {
      this.isClick = item.value
      this.$emit('getTabIndex', item.value)
    }
  }
}



